export function postMethod() {
	return Liferay.Util.fetch(
			"http://localhost:8080/o/headless-delivery/v1.0/sites/20121/blog-postings/",
			{
			  method: "POST",
			  headers: {
				"Content-Type": "application/json",
				Authorization: "Basic " + btoa("test@liferay.com:teste"),
			  },
			  body: JSON.stringify({
				headline: "Testing Blog Posting",
				articleBody:
				  "This article was posted via REST services based on what Abhner is learning",
			  }),
			}
		  )
			.then((postResponse) => postResponse.json())
			.then((data) => console.log(data));
}
